.korean-game {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.score {
  font-size: 1.5rem;
  margin: 20px 0;
  font-weight: bold;
}

.current-word {
  margin: 30px 0;
}

.current-word h2 {
  font-size: 3rem;
  margin: 0;
  color: #2c3e50;
}

.hint {
  color: #7f8c8d;
  font-style: italic;
}

.message {
  min-height: 30px;
  font-size: 1.2rem;
  margin: 10px 0;
  color: #27ae60;
}

.image-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 30px;
}

.image-container {
  cursor: pointer;
  transition: transform 0.2s;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.image-container:hover {
  transform: scale(1.05);
}

.image-container img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  display: block;
} 